import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { StoresService } from '../stores.service';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';

import { PlacesService } from '../../places/places.service';
import { InputSelectItem } from 'src/app/models/InputSelectItem';

@Component({
  selector: 'app-add-store',
  templateUrl: './add-store.component.html',
  styleUrls: ['./add-store.component.scss'],
})
export class AddStoreComponent implements OnInit {
  image;
  constructor(
    private fb: FormBuilder,
    private storesService: StoresService,
    private placesService: PlacesService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {}

  form = this.fb.group({
    image: [],
    name: ['', Validators.required],
    address: ['', Validators.required],
    detailedAddress: ['', Validators.required],
  });

  ngOnInit(): void {
    this.getPlacesList();
  }

  // Start select dropdown
  selectPlacesList: InputSelectItem[];
  selectedPlaceId: string;
  getPlacesList() {
    this.spinner.show();

    this.placesService.getAll();

    this.placesService.places.subscribe((data) => {
      if (data) {
        this.selectPlacesList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']}`,
            nameAr: ``,
          };
        });
      }
    });

    this.spinner.hide();
  }
  selectPlace(event) {
    if (event) {
      this.selectedPlaceId = event._id;
      this.form.get('address').patchValue(event._id)
    }
    console.log(this.form.value);
  }
  // End select dropdown

  onSubmit(formValue) {
    this.spinner.show();
    const formData = new FormData();

    for (const key in formValue) {
      if (formValue[key] instanceof File) {
        formData.append(key, formValue[key]);
      } else {
        // contains object
        formData.append(key, JSON.stringify(formValue[key]));
      }
    }

    this.storesService.addOne(formData).subscribe(
      (res) => {
        this.image = null;
        this.form.reset();
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['/stores']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
    this.spinner.hide();
  }
}
