import { Component, OnInit, AfterViewInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { StoresService } from '../stores.service';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';

import { PlacesService } from '../../places/places.service';
import { InputSelectItem } from 'src/app/models/InputSelectItem';

@Component({
  selector: 'app-edit-store',
  templateUrl: './edit-store.component.html',
  styleUrls: ['./edit-store.component.scss'],
})
export class EditStoreComponent implements OnInit, AfterViewInit {
  image;
  storeId;
  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private storesService: StoresService,
    private placesService: PlacesService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {}

  form = this.fb.group({
    image: [],
    name: ['', Validators.required],
    // address: ['', Validators.required],
    profitType: [''],
    profitValue: [0],
    profitPercentage: [0],
    discountType: [''],
    discountValue: [0],
    discountPercentage: [0],
  });

  ngOnInit(): void {
    this.storeId = this.route.snapshot.paramMap.get('id');
  }
  ngAfterViewInit() {
    this.getOne(this.storeId);
  }

  // Start select dropdown
  selectPlacesList: InputSelectItem[];
  selectedPlaceId: string;
  initialSelectedPlace: InputSelectItem;

  getPlacesList() {
    this.spinner.show();

    this.placesService.getAll();

    this.placesService.places.subscribe((data) => {
      if (data) {
        this.selectPlacesList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']}`,
            nameAr: ``,
          };
        });
      }
    });

    this.spinner.hide();
  }
  selectPlace(event) {
    if (event) {
      this.selectedPlaceId = event._id;
      this.form.get('address').patchValue(event._id);
    }
    console.log(this.form.value);
  }
  // End select dropdown

  getOne(id) {
    this.spinner.show();

    this.storesService.getOne(id).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.image = res['data']['image'];

        this.form.patchValue(res['data']);

        this.form.get('image').patchValue(res['data']['image']);
        this.form.get('name').patchValue(res['data']['name']);
        // if (res['data']['address']) {
        //   this.initialSelectedPlace = {
        //     _id: res['data']['address']['_id'],
        //     nameEn: res['data']['address']['name'],
        //     nameAr: '',
        //   };
        //   this.form.get('address').patchValue(this.initialSelectedPlace._id);
        // }
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  onSubmit(formValue) {
    this.spinner.show();

    const formData = new FormData();

    for (const key in formValue) {
      if (formValue[key] instanceof File) {
        formData.append(key, formValue[key]);
      } else {
        // contains object
        formData.append(key, JSON.stringify(formValue[key]));
      }
    }

    this.storesService.updateOne(formData, this.storeId).subscribe(
      (res) => {
        console.log(res);
        this.image = null;
        this.form.reset();
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['/stores']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }
}
